<template>
    <div class="wrapper">
        <div class="wrapper_header">
            <h2>Table UI</h2>
        </div>
        <div class="settings">

            <div class="left_settings">
                <div class='left_settings_item'>
                    Sortings by:
                </div>
                <div class='left_settings_item'
                     v-for="field in fields"
                     :key ="field"
                     :value="field"
                     :class="{'left_settings_item_active':field=== sortField}"
                     @click="sort(field)"
                >{{field}}</div>

            </div>

            <div class="right_settings">
                <div class="right_settings_item">
                    <button class="deleteBlock"
                            :disabled="selectedProducts.length ==0"
                            :class="{'greenButton':selectedProducts.length !==0}"
                            @click="isShowDeleteItems = true"
                    >Delete {{selectedProducts.length!==0 ? `(${selectedProducts.length})` : null}}
                    </button>
                    <div class="deleteBox"
                         v-if="isShowDeleteItems">
                        <div class="deleteText"> Are you sure you want to <strong>delete items?</strong></div>
                        <div class="deleteButtons">
                            <div class="cancelButton" @click="isShowDeleteItems = false">Cancel</div>
                            <div class="confirmButton" @click="deleteSelectedItems">Confirm</div>
                        </div>
                    </div>
                </div>
                <div class="right_settings_item">

                    <div class="select_header"
                            @click="isShowSelectBody =!isShowSelectBody">
                        <div class="select_header_content">
                            {{countPerPage}} Per Page
                        </div>
                        <div class="arrow_down">
                            <span class="material-icons ">keyboard_arrow_down</span>
                        </div>
                    </div>
                    <div class="select_body"
                         v-if="isShowSelectBody">
                        <div class="select_body_item"
                                v-for="(option,index) in countOptions"
                             :key="index"
                             @click="changeCountPerPage(option)"
                        >{{option}} Per Page
                        </div>
                    </div>

                </div>
                <div class="right_settings_item">
                    <div class="pagination" @click="pagePrev">
                        <button class="arrow"
                                :disabled="currentPage === 1">
                            <span class="material-icons">keyboard_arrow_left</span>
                        </button>
                    </div>
                </div>
                <div class="right_settings_item page_count">
                    <strong>{{pointSlice}}</strong>
                    -
                    <strong>{{pointSlice2}}</strong>
                    of
                    <strong>{{totalCount}}</strong>
                </div>
                <div class="right_settings_item right_arrow">
                    <div class="pagination" @click="pageNext">
                        <button class="arrow"
                                :disabled="currentPage === pages">
                            <span class="material-icons">keyboard_arrow_right</span>
                        </button>
                    </div>
                </div>

                <div class="right_settings_item">
                    <div class="selectColumnFields" @click="showColumnCheckboxes()">
                        <div class="selectColumnContent">
                            {{selectedColumns.length}} columns selected
                        </div>
                        <div class="arrow_down">
                            <span class="material-icons ">keyboard_arrow_down</span>
                        </div>
                    </div>
                    <div class="columns_checkboxes"
                         v-if="isShowSelectWithCheckboxes">
                        <label class="checkbox_label">
                            <input type="checkbox"
                                   class="checkbox_input"
                                   @click="checkColumns()"
                                   v-model="isCheckColumns"/>
                            <span class="checkmark"></span>
                            <strong>Select All</strong>
                        </label>
                        <div v-for="field in fields"
                             :key="field">
                            <label class="checkbox_label">
                                <input type="checkbox"
                                       class="checkbox_input"
                                       :value="field"
                                       v-model="selectedColumns"
                                       @change="updateCheckColumns()"/>
                                <span class="checkmark"></span>
                                {{field}}
                            </label>
                        </div>
                    </div>
                </div>
            </div>

        </div>
        <div class="main_table">
            <div class="table_header">
                <div class="header table_header_checkbox">
                    <label class="product_checkbox_label">
                        <input type="checkbox"
                               class="product_checkbox_input"
                               @click="checkAll()"
                               v-model="isCheckAll"/>
                        <span class="product_checkmark"></span>
                    </label>
                </div>

                <div class="header table_header_product"
                     v-if="selectedColumns.includes('Product (100g serving)')"
                     :class="{'greenText':sortField ==='Product (100g serving)'}"
                >Product (100g serving)</div>
                <div class="header table_header_calories"
                     v-if="selectedColumns.includes('Calories')"
                     :class="{'greenText':sortField ==='Calories'}"
                >Calories</div>
                <div class="header table_header_fat"
                     v-if="selectedColumns.includes('Fat (g)')"
                     :class="{'greenText':sortField ==='Fat (g)'}"
                >Fat (g)</div>
                <div class="header table_header_carbs"
                     v-if="selectedColumns.includes('Carbs (g)')"
                     :class="{'greenText':sortField ==='Carbs (g)'}"
                >Carbs (g)</div>
                <div class="header table_header_protein"
                     v-if="selectedColumns.includes('Protein (g)')"
                     :class="{'greenText':sortField ==='Protein (g)'}"
                >Protein (g)</div>
                <div class="header table_header_iron"
                     v-if="selectedColumns.includes('Iron (%)')"
                     :class="{'greenText':sortField ==='Iron (%)'}"
                >Iron (%)</div>
            </div>
            <div id="table_body">
                <div class="table_row"
                     v-for="(product, index) in paginatedProducts"
                     :key="product.product"
                     :class="{'row_zebra':index % 2 !== 0,'active_row': index === activeRow}"
                     @mouseover="setActiveRow(index)"
                     @mouseleave="setActiveRow(null)"
                >
                    <div class="left_part_row">
                        <div class="row row_checkbox">
                            <label class="product_checkbox_label">
                                <input type="checkbox"
                                       class="product_checkbox_input"
                                       :value="product"
                                       v-model="selectedProducts"
                                       @change="updateCheckAll()"/>
                                <span class="product_checkmark"></span>
                            </label>
                        </div>
                        <div class="row row_product"
                             v-if="selectedColumns.includes('Product (100g serving)')">{{product.product}}
                        </div>
                        <div class="row row_calories"
                             v-if="selectedColumns.includes('Calories')">{{product.calories}}
                        </div>
                        <div class="row row_fat"
                             v-if="selectedColumns.includes('Fat (g)')">{{product.fat}}
                        </div>
                        <div class="row row_carbs"
                             v-if="selectedColumns.includes('Carbs (g)')">{{product.carbs}}
                        </div>
                        <div class="row row_protein"
                             v-if="selectedColumns.includes('Protein (g)')">{{product.protein}}
                        </div>
                        <div class="row row_iron"
                             v-if="selectedColumns.includes('Iron (%)')">{{product.iron}}
                        </div>
                    </div>
                    <div class="right_part_row">
                        <div class="delete_icon_block"
                             v-if="index===activeRow"
                             @click="numberShowDeleteBox = index">
                            <div class="delete_icon">
                                <span class="material-icons md-light">delete</span>
                            </div>
                            <div>delete</div>
                        </div>
                        <div class="deleteBox"
                             v-if="index === numberShowDeleteBox">
                            <div class="deleteText"> Are you sure you want to <strong>delete item?</strong></div>
                            <div class="deleteButtons">
                                <div class="cancelButton" @click="numberShowDeleteBox = null">Cancel</div>
                                <div class="confirmButton"
                                     @click="deleteSelected((currentPage-1)*countPerPage+index)"
                                >Confirm</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters,mapActions} from 'vuex'
    export default {
    name: "Table",
    data(){
        return {
            sortField:'Product (100g serving)',
            fields:['Product (100g serving)', 'Calories','Fat (g)','Carbs (g)','Protein (g)','Iron (%)'],
            currentPage: 1,
            countPerPage: 10,
            countOptions:[10,15,20],
            isCheckAll:false,
            selectedProducts:[],
            isShowSelectBody:false,
            isShowSelectWithCheckboxes: false,
            isCheckColumns:true,
            selectedColumns:['Product (100g serving)', 'Calories','Fat (g)','Carbs (g)','Protein (g)','Iron (%)'],
            numberShowDeleteBox:null,
            isShowDeleteItems:false,
            activeRow:null
        }
    },
    computed: {
        ...mapGetters(['products']),
        totalCount(){
            return this.products.length
        },
        pages(){
            return Math.ceil(this.products.length/this.countPerPage)
        },
        pointSlice(){
            return (this.currentPage - 1) * this.countPerPage
        },
        pointSlice2(){
            return this.currentPage*this.countPerPage
        },
        paginatedProducts(){
            return this.products.slice(this.pointSlice, this.pointSlice2)
        }
    },


    methods: {
        ...mapActions(['deleteSelectedProducts']),
        deleteSelected(index){
            this.numberShowDeleteBox = null
            this.deleteSelectedProducts(index)
            this.updateCheckAll()
        },
        deleteSelectedItems(){
            this.selectedProducts.forEach(item =>this.deleteSelected(item))
            this.selectedProducts=[]
            this.isShowDeleteItems = false
            this.updateCheckAll()
        },
        checkAll(){
            this.isCheckAll = !this.isCheckAll
            this.selectedProducts=[]
            if(this.isCheckAll){
                this.paginatedProducts.forEach(item =>this.selectedProducts.push(item))
            }
        },
        updateCheckAll(){
            if(this.selectedProducts.length == this.countPerPage){
                this.isCheckAll = true
            }else{
                this.isCheckAll =false
            }
        },
        pageNext() {
            if (this.currentPage<this.pages) { this.currentPage++}
        },
        pagePrev() {
            if (this.currentPage>1){this.currentPage--}
        },
        changeCountPerPage(count){
            this.countPerPage = count
            this.isShowSelectBody = false
        },
        showColumnCheckboxes(){
            this.isShowSelectWithCheckboxes = !this.isShowSelectWithCheckboxes;
        },
        checkColumns(){
            this.isCheckColumns = !this.isCheckColumns
            this.selectedColumns=[]
            if(this.isCheckColumns){
                this.fields.forEach(item =>this.selectedColumns.push(item))
            }
        },
        updateCheckColumns(){
            console.log(this.selectedColumns)
            if(this.selectedColumns.length == this.fields.length){
                this.isCheckColumns = true
            }else{
                this.isCheckColumns =false
            }
        },
        setActiveRow(index){
            console.log(this.numberShowDeleteBox)
           if (this.numberShowDeleteBox==null) {this.activeRow = index}
        },
        sort(field) {
            this.sortField = field
            switch (field) {
                case 'Product (100g serving)':{
                   return this.products.sort((a,b)=> a.product.localeCompare(b.product))
                }
                case 'Calories':{
                    console.log('Calories')
                   return this.products.sort((a,b)=> a.calories - b.calories)
                }
                case 'Fat(g)':{
                   return this.products.sort((a,b)=> a.fat-b.fat)
                }
                case 'Carbs(g)':{
                   return this.products.sort((a,b)=> a.carbs-b.carbs)
                }
                case 'Protein (g)':{
                   return this.products.sort((a,b)=> a.protein-b.protein)
                }
                case 'Iron (%)':{
                   return this.products.sort((a,b)=> a.iron-b.iron)
                }

            }
        }
    }
}
</script>

