import Vue from 'vue'
import Vuex from 'vuex'
import productsFromFile from '../api/products.json';


Vue.use(Vuex)

/** True = 65%, False = 35% */
const rejectByChance = () => {
    return Math.random() <= 0.35;

};

/** Emulate get request */
const getProducts = () => new Promise((resolve, reject) => {
    if (rejectByChance()) {
        return reject({
            error: 'Server error'
        });
    }
    const delay = parseInt(Math.random() * 1000);
    setTimeout(() => {
        resolve(productsFromFile);
    }, delay);
});

/** Emulate delete request */
