<template>
  <div id="app">
    <Loader v-if="isLoading" class="loader"></Loader>
    <div v-else>
      <Table v-if="isInitialized"></Table>
      <div v-else class="error">
        <h3> Server is overloaded. Please, update page</h3>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters,mapActions} from 'vuex'
  import Loader from "./components/Loader";
  import Table from "./components/Table";
  export default {
    name: "Home",
    components: {
      'Loader':Loader,
      'Table': Table
    },
    async mounted(){
      this.fetchProducts()
    },
    computed: {
      ...mapGetters(['isLoading','isInitialized'])
    },
    methods: {
      ...mapActions(['fetchProducts'])
    }
  }

</script>
